8px;\n    letter-spacing: 0em;\n    text-align: left;\n    height: 28px;\n    display: flex;\n    justify-content: center;\n    align-items: center;\n    cursor: default;\n    border-top: $btn_border;\n    border-bottom: $btn_border;\n    background: white;\n    color: $table_text;\n\n    &:not(:last-child) {\n      border-right: $btn_border;\n    }\n\n    &--top {\n      background: rgba(156, 156, 156, 1) !important;\n      //color: white;\n      //background: $table_bg !important;\n    }\n  }\n\n  &:last-child {\n    border-right: none;\n  }\n\n  &-form {\n\n    input {\n      //color: $button_color;\n    }\n  }\n\n  &__use {\n    label {\n      cursor: pointer;\n      transition: color .3s;\n\n      &:hover {\n        color: $main_green;\n        transition: color .3s;\n      }\n    }\n  }\n\n  &__data {\n    font-size: 14px;\n    font-weight: 400;\n    height: 28px;\n    width: 100%;\n    text-align: center;\n    border-radius: 1px;\n    border: none;\n    background: transparent;\n    color: inherit;\n    outline: 3.3px $button_color;\n    position: relative;\n\n    &--ro {\n      cursor: default;\n      outline: none;\n    }\n\n    &--chosen {\n      //font-size: 15px;\n      background: $table_bg_main;\n      color: $table_text;\n    }\n\n    &--opened {\n      height: 56px;\n      font-size: 15px;\n      background: $table_bg_main;\n      color: $table_text;\n      border-bottom: $btn_border;\n    }\n\n    //&--opened.table__data--current {\n    //  border-bottom: 2px solid black;\n    //}\n\n    &--current {\n      font-weight: 600;\n      color: $table_text;\n      border: 2px solid black;\n      //font-size: 16px;\n    }\n  }\n\n  &__db {\n    display: flex;\n    min-width: 70px;\n    max-width: 70px;\n  }\n\n  &__timestamp {\n    min-width: 110px;\n    max-width: 110px;\n\n    &--time {\n      min-width: 150px;\n      max-width: 150px;\n    }\n  }\n\n  &__files {\n    min-width: 45px;\n    max-width: 45px;\n    position: relative;\n  }\n\n  &__number {\n    min-width: 120px;\n    max-width: 120px;\n  }\n\n  &__sample {\n    min-width: 60px;\n    max-width: 60px;\n  }\n\n  &__client {\n    min-width: 160px;\n    max-width: 160px;\n  }\n\n  &__name {\n    min-width: 260px;\n    max-width: 260px;\n  }\n\n  &__material {\n    min-width: 140px;\n    max-width: 140px;\n  }\n\n  &__quantity {\n    min-width: 70px;\n    max-width: 70px;\n  }\n\n  &__issued {\n    min-width: 70px;\n    max-width: 70px;\n    border-right: $btn_border !important;\n\n    &--done {\n      color: green;\n      font-size: 16px;\n      font-weight: 600;\n      //animation: issued-ready infinite 4s;\n    }\n  }\n\n  &__complete {\n    border-right: none !important;\n    border-top: $btn_border;\n    border-bottom: $btn_border;\n\n    input {\n      height: 26px;\n    }\n  }\n\n  &__m {\n    min-width: 36px;\n    max-width: 36px;\n  }\n\n  &__endtime {\n    min-width: 140px;\n    max-width: 140px;\n\n    &--dead {\n      color: red;\n      font-size: 15px;\n    }\n  }\n\n  &__route {\n    display: flex;\n    align-items: center;\n    justify-content: center;\n    height: 29px;\n    min-width: 50px;\n    max-width: 50px;\n\n    &:last-child {\n      border-right: $btn_border !important;\n    }\n  }\n\n  &__routes {\n    min-width: 534px;\n    max-width: 534px;\n    position: relative;\n    display: flex;\n    align-items: center\n    //overflow: hidden;\n  }\n\n  &-routes__issued {\n    //border-top: 1px solid black;\n  }\n\n  &__p {\n    min-width: 60px;\n    max-width: 60px;\n  }\n\n  &-p-select,\n  &-m-select {\n    -webkit-appearance: none;\n    -moz-appearance: none;\n    appearance: none;\n    cursor: pointer;\n  }\n\n  &__comment {\n    flex-grow: 1;\n    min-width: 200px;\n    max-width: 100%;\n\n    &--top {\n      flex-grow: 1;\n      min-width: 200px;\n      max-width: 100%;\n    }\n  }\n\n  &-routes {\n    &__wrapper {\n      position: relative;\n      display: flex;\n      align-items: center;\n      //justify-content: center;\n      hei